
























import { Component, Vue } from "vue-property-decorator";
interface User {
  username: String;
  nickname: String;
  email: String;
  password: String;
}

@Component
export default class signup extends Vue {
  // data
  public user: User = {
    username: "",
    nickname: "",
    email: "",
    password: ""
  };

  // methods
  public signup() {
    let { username, nickname, email, password } = this.user;
    this.axios
      .post("/user/signup", {
        username,
        nickname,
        email,
        password
      })
      .then((res: any) => {
        localStorage.setItem("token", res.token);
        this.$message({
          message: "注册成功",
          type: "success"
        });
        this.$router.push("/");
      })
      .catch((err: any) => {
        console.log(err.response);
        if (err.response.status === 400) {
          const data = err.response.data;
          const detail = data.details[0].path[0];
          switch (detail) {
            case "username":
              this.$message("用户名必须为4-16位的英文字母和数字的组合");
              break;
            case "nickname":
              this.$message("昵称不能为空");
              break;
            case "email":
              this.$message("请输入正确的邮箱");
              break;
            case "password":
              this.$message("密码必须为6-16位的英文字母和数字的组合");
              break;
          }
        } else if (err.response.status === 403) {
          this.$message("该用户已存在");
        } else {
          this.$message("网络连接错误");
        }
      });
  }
}
